/* eslint-disable class-methods-use-this */
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getTutorialSequenceGridConfig } from '../../configs/grid-configs';
import { getTutorialCharectorImageConfig } from '../../configs/image-configs';
import { getTutorialTextConfig } from '../../configs/text-configs';
import { InputPriority } from '../../constants';
import { makeImage, makeText } from '../../utils';

export class TutorialSequenceView extends Phaser2Grid {
  constructor(config, index) {
    super(CI_API.game);

    this._showTween = null;
    this._hideTween = null;

    this._config = config;
    this._index = index;
    this.alpha = 0;

    this._build();
  }

  getGridConfig() {
    return getTutorialSequenceGridConfig();
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);

    super.destroy();
  }

  show() {
    this._showTween = this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true);
    return this._showTween;
  }

  hide() {
    this._hideTween = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    return this._hideTween;
  }

  _build() {
    super.build(this.getGridConfig());
    this._buildBg();
    this._buildLabel();
    this._buildCharector();
  }

  _buildCharector() {
    const charector = makeImage(getTutorialCharectorImageConfig(CI_API.Globals.PARAMS.tutorial_character));
    this.setChild('charector', charector);
  }

  _buildBg() {
    const gr = this.game.add.graphics();
    gr.beginFill(0xffffc1);
    gr.drawRect(0, 0, 10, 10);
    gr.endFill();

    this.setChild('bg', (this._bg = gr));
    this._bg.inputEnabled = true;
    this._bg.input.priorityID = InputPriority.Foreground;
  }

  _buildLabel() {
    const { text } = this._config;
    const label = makeText(getTutorialTextConfig(text, CI_API.Globals.PARAMS.merge_character));

    this.setChild('content', (this._label = label));
  }
}
