/* eslint-disable class-methods-use-this */
import { HEIGHT, WIDTH } from '../../../configs/item-config';
import { ISO_ANGLE } from '../../../constants';
import { makeText } from '../../../utils';
import { Rhombus } from '../../../utils/geom/rhombus';

export const CELL_CENTER_X = -45;
export const CELL_CENTER_Y = 16;

export class CellView extends Phaser.Graphics {
  constructor(row, col, uuid) {
    super(CI_API.game);

    this._uuid = uuid;
    this._row = row;
    this._col = col;
    this.alpha = 0.3;
    // this.angle = 20;
    // this._shape = new Rhombus(-WIDTH / 2, -HEIGHT / 2, WIDTH, HEIGHT);
    this._shape = new Rhombus(0, 0, WIDTH, HEIGHT, 119);
    this._build(row, col);
  }

  get uuid() {
    return this._uuid;
  }

  get shape() {
    return this._shape;
  }

  get row() {
    return this._row;
  }

  get col() {
    return this._col;
  }

  getBounds() {
    return this._boundsShape.getBounds();
  }

  _buildBoundsShape() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x0000ff, 0);
    gr.drawCircle(-55, 10, 60);
    this.addChild((this._boundsShape = gr));
  }

  _build(row, col) {
    this._buildBoundsShape();

    const text = makeText({
      text: {
        text: `${row},${col}`,
        ignoreLocalization: true,
        toString: function toString() {
          return this;
        }
      }
    });

    text.visible = false;

    this.beginFill(Math.random() * 0xffffff, 0);
    this.drawPolygon(this._shape.points);
    this.endFill();
    text.anchor.set(0.5);
    text.angle = -ISO_ANGLE;
    text.position.set(CELL_CENTER_X, CELL_CENTER_Y);
    this.addChild(text);
  }
}
