import { lego } from '@armathai/lego';
import { BoardState, CellState } from '../../constants';
import { store } from '../../models/store';
import { itemActionDoneCommand } from '../board/item-action-done-command';
import { setBoardStateCommand } from '../board/set-board-state-command';
import { setCellStateCommand } from '../board/set-cell-state-command';
import { setItemHighlightCommand } from '../board/set-item-highlight-command';

export function onDragStopCommand(to, item) {
  const draggedCells = store.game.boardModel.getCellsByState(CellState.Drag);
  lego.command.payload(BoardState.DragStop).execute(setBoardStateCommand);

  draggedCells.forEach(cell => {
    lego.command
      .payload(item.uuid, false)
      .execute(setItemHighlightCommand)

      .payload(CellState.DragStop, cell.uuid)
      .execute(setCellStateCommand)

      .payload(CellState.Idle, cell.uuid)
      .execute(setCellStateCommand);
  });

  const from = store.game.boardModel.getCellByItemUuid(item.uuid);
  if (!to) {
    lego.command
      .payload(BoardState.ActionReject)
      .execute(setBoardStateCommand)

      .payload(CellState.Reject, from.uuid)
      .execute(setCellStateCommand);
  } else {
    lego.command.payload(from, to, item).execute(itemActionDoneCommand);
    // check for merge
  }
}
