export const Spines = {
  BeeBig: {
    Key: 'bee_big',
    Animations: {
      Idle: 'idle',
      Move: 'idle_move'
    }
  },
  BeeSmall: {
    Key: 'bee_small',
    Animations: {
      Idle: 'idle'
    }
  },
  HummingbirdBig: {
    Key: 'hummingbird_big',
    Animations: {
      Move: 'idle_move',
      Idle: 'idle'
    }
  },
  HummingbirdSmall: {
    Key: 'hummingbird_small',
    Animations: {
      Idle: 'idle'
    }
  },
  SquirelBig: {
    Key: 'squirel_big',
    Animations: {
      Move: 'idle_move',
      Idle: 'idle'
    }
  },
  SquirelSmall: {
    Key: 'squirel_small',
    Animations: {
      Idle: 'idle'
    }
  }
};

export function getBeeSmallSpineConfig(x, y) {
  return {
    key: Spines.BeeSmall.Key,
    x,
    y
  };
}

export function getBeeBigSpineConfig(x, y) {
  return {
    key: Spines.BeeBig.Key,
    x,
    y
  };
}

export function getHummingbirdSmallSpineConfig(x, y) {
  return {
    key: Spines.HummingbirdSmall.Key,
    x,
    y
  };
}

export function getHummingbirdBigSpineConfig(x, y) {
  return {
    key: Spines.HummingbirdBig.Key,
    x,
    y
  };
}

export function getSquirelBigSpineConfig(x, y) {
  return {
    key: Spines.SquirelBig.Key,
    x,
    y
  };
}

export function getSquirelSmallSpineConfig(x, y) {
  return {
    key: Spines.SquirelSmall.Key,
    x,
    y
  };
}
