// @ts-nocheck
/* eslint-disable class-methods-use-this */

import { fitText, makeImage, makeNinePatch, makeText } from '.';
import { getPopupBgImageConfig, getPopupShineImageConfig } from '../configs/image-configs';
import { getPopupHeaderPatchConfig } from '../configs/ninepatch-configs';
import { Container } from './container';

export class PopupComponent extends Container {
  constructor(config) {
    super();

    this._config = config;
    this._build();
  }

  _build() {
    this._buildBg();
    this._buildHeader();
    this._buildShine();
  }

  _buildBg() {
    const bg = makeImage(getPopupBgImageConfig());

    this.addChild((this._bg = bg));
  }

  _buildShine() {
    const glow = makeImage(getPopupShineImageConfig());
    glow.position.set(3, -10);
    const tw = this.game.add.tween(glow).to({ alpha: 0.01 }, 1500, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
    tw.universal = true;

    this.addChild(glow);
  }

  _buildHeader() {
    const { textConfig } = this._config.headerConfig;
    const header = makeNinePatch(getPopupHeaderPatchConfig(650));
    const label = makeText(textConfig);
    label.anchor.set(0.5);
    fitText(label, header.width * 0.7, header.height * 0.75);

    header.addChild(label);

    header.centerY = this._bg.top + 10;
    this.addChild((this._header = header));
  }
}
