import { InputPriority } from '../constants';
import { getCtaBtnImageConfig, getPersistentCtaButtonImageConfig } from './image-configs';
import {
  getCtaStyle1PlayButtonTextConfig,
  getCtaStyle2PlayButtonTextConfig,
  getPersistentCtaTextConfig
} from './text-configs';

// PERSISTENT CTA
export function getPersistentCtaButtonConfig() {
  return {
    input: {
      name: 'persistent_cta_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getPersistentCtaButtonImageConfig(),
        label: getPersistentCtaTextConfig(),
        fitWidth: 0.7,
        fitHeight: 0.7
      }
    }
  };
}

// CTA
export function getCtaStylePlayButtonConfig(fit = 0.8, smallText = false) {
  return {
    input: {
      name: 'cta_play_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaBtnImageConfig(),
        label: smallText ? getCtaStyle2PlayButtonTextConfig() : getCtaStyle1PlayButtonTextConfig(),
        fitWidth: fit,
        fitHeight: fit
      }
    }
  };
}
