import { CellState } from '../../constants';

export const identityFilter = (cells, i, j, identity) => {
  return cells[i][j].item.type === identity;
};

export const hasCellFilter = (cells, i, j) => {
  return cells[i][j];
};

export const hasItemFilter = (cells, i, j) => {
  return cells[i][j].item;
};

export const notEmptyFilter = (cells, i, j) => {
  return !cells[i][j].isEmpty;
};

export const idleStateFilter = (cells, i, j) => {
  return cells[i][j].state === CellState.Idle;
};

export const clusterFilter = (cells, i, j, identity) => {
  return (
    hasCellFilter(cells, i, j) &&
    hasItemFilter(cells, i, j) &&
    notEmptyFilter(cells, i, j) &&
    idleStateFilter(cells, i, j) &&
    identityFilter(cells, i, j, identity)
  );
};
