import { getItemGlowImageConfig, getItemHighlightImageConfig } from '../../../configs/image-configs';
import { makeImage } from '../../../utils';
import { ItemComponent } from './item-component';

export class NectarItemComponent extends ItemComponent {
  constructor() {
    super('nectar', { x: 0.5, y: 0.6 });
    this._img.scale.set(0.7);
    this._buildGlowItem();
  }

  startActivate() {
    this._glowFx.visible = true;
    this._glowFx.alpha = 0;
    const tw = this.game.add.tween(this._glowFx).to({ alpha: 0.8 }, 500, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;
    tw.onComplete.add(() => {
      const pulseTw = this.game.add
        .tween(this._glowFx)
        .to({ alpha: 0.4 }, 600, Phaser.Easing.Sinusoidal.InOut, true, 300, -1, true);
      pulseTw.universal = true;
    });
  }

  _buildGlowItem() {
    const glowItem = makeImage(getItemGlowImageConfig());
    glowItem.visible = false;
    glowItem.anchor.set(this._anchor.x, this._anchor.y);
    glowItem.scale.set(0.7);
    this.addChild((this._glowFx = glowItem));
  }

  _buildHighlight() {
    const highlight = makeImage(getItemHighlightImageConfig());
    highlight.anchor.set(this._anchor.x, this._anchor.y);
    highlight.position.set(-4, 3);
    highlight.alpha = 0;
    highlight.visible = false;
    this.addChild((this._highlight = highlight));
  }
}
