/* eslint-disable class-methods-use-this */
/* eslint-disable no-undef */
import { getItemShadowImageConfig } from '../../../configs/image-configs';
import {
  getBeeSmallSpineConfig,
  getHummingbirdSmallSpineConfig,
  getSquirelSmallSpineConfig,
  Spines
} from '../../../configs/spine-config';
import { delayRunnable, makeImage, makeSpine } from '../../../utils';
import { randomInt } from '../../../utils/number/random-int';
import { ItemComponent } from './item-component';

export const SmallSpinsConfigArray = {
  hummingbird: getHummingbirdSmallSpineConfig,
  bee: getBeeSmallSpineConfig,
  flyingsquirrel: getSquirelSmallSpineConfig
};
export class SmallItemComponent extends ItemComponent {
  constructor() {
    super(`small_${CI_API.Globals.PARAMS.merge_character}`, { x: 0.5, y: 0.8 });

    this._item.scale.set(0.7);
  }

  getSpineConfig() {
    return SmallSpinsConfigArray[CI_API.Globals.PARAMS.merge_character]();
  }

  _build() {
    this._buildHighlight();
    this._buildShadow();
    this._buildItem();
  }

  _buildShadow() {
    const img = makeImage(getItemShadowImageConfig());
    img.scale.set(0.4);
    this.addChild(img);
  }

  _buildItem() {
    this._item = makeSpine(this.getSpineConfig());
    delayRunnable(randomInt(0, 300), () => {
      this._item.setAnimationByName(0, Spines.BeeSmall.Animations.Idle, true);
    });
    this._item.position.set(0, -35);

    this.addChild(this._item);
  }
}
