import { lego } from '@armathai/lego';
import { ItemsConfig, ItemType } from '../../configs/item-config';
import { BoardState, CellState } from '../../constants';
import { store } from '../../models/store';
import { setBoardStateCommand } from '../board/set-board-state-command';
import { setCellStateCommand } from '../board/set-cell-state-command';
import { setItemHighlightCommand } from '../board/set-item-highlight-command';

export function onBoardItemClickedCommand(cell, item) {
  const { boardModel } = store.game;
  lego.command
    .payload(item.uuid, false)
    .execute(setItemHighlightCommand)

    .payload(CellState.Idle, cell.uuid)
    .execute(setCellStateCommand)

    .payload(BoardState.Idle)
    .execute(setBoardStateCommand);
  // if (CI_API.Globals.PARAMS.tap_to_merge) {
  //   lego.command
  //     .payload(item.uuid, false)
  //     .execute(setItemHighlightCommand)

  //     .payload(CellState.Idle, cell.uuid)
  //     .execute(setCellStateCommand)

  //     .payload(BoardState.Idle)
  //     .execute(setBoardStateCommand);
  // }

  const cells = boardModel.getCellsByState(CellState.Idle).filter(c => c.item && c.item.type === item.type);
  const { clickToActivate } = ItemsConfig[item.type];
  if (clickToActivate) {
    if (item.type === ItemType.flower) {
      const cellsWithNecessaryItems = boardModel
        .getCellsByState(CellState.Idle)
        .filter(c => c.item && c.item.type === ItemType.big);

      // cellsWithNecessaryItems.length === 0
      //   ? (cellsWithNecessaryItems = [...boardModel.cells.find(c => c.item && c.item.type == ItemType.big)])
      //   : false;
      if (cellsWithNecessaryItems.length) {
        boardModel.gainNectar(cellsWithNecessaryItems[0], cell);
      }
    } else if (item.type === ItemType.nectar) {
      boardModel.clearClouds();
    } else if (item.type === ItemType.big) {
      let flowerCell = {};
      boardModel.cells.forEach(row => {
        const cellA = row.find(c => c.item && c.item.type === ItemType.flower);
        if (cellA) flowerCell = cellA;
      });
      boardModel.gainNectar(cell, flowerCell);
    }
  } else if (cells.length >= 3) {
    boardModel.mergeItems(cells, item, cell);
    // if (CI_API.Globals.PARAMS.tap_to_merge) {
    //   boardModel.mergeItems(cells, item, cell);
    // }
  }
}
