/* eslint-disable class-methods-use-this */
import { getItemShadowImageConfig } from '../../../configs/image-configs';
import {
  getBeeBigSpineConfig,
  getHummingbirdBigSpineConfig,
  getSquirelBigSpineConfig,
  Spines
} from '../../../configs/spine-config';
import { makeImage, makeSpine } from '../../../utils';
import { ItemComponent } from './item-component';

export const BigSpinsConfigArray = {
  hummingbird: getHummingbirdBigSpineConfig,
  bee: getBeeBigSpineConfig,
  flyingsquirrel: getSquirelBigSpineConfig
};
export class BigItemComponent extends ItemComponent {
  constructor() {
    super(`big_${CI_API.Globals.PARAMS.merge_character}`, { x: 0.5, y: 0.8 });
    this._item.scale.set(0.8);
  }

  _getSpineConfig() {
    return BigSpinsConfigArray[CI_API.Globals.PARAMS.merge_character]();
  }

  _build() {
    this._buildItem();
    this._buildShadow();
  }

  _buildShadow() {
    const img = makeImage(getItemShadowImageConfig());
    this.addChild(img);
  }

  _buildItem() {
    this._item = makeSpine(this._getSpineConfig());
    this._item.setAnimationByName(0, Spines.BeeBig.Animations.Idle, true);
    this._item.position.set(0, -50);
    this.addChild(this._item);
  }
}
