/* eslint-disable no-lonely-if */
import { getInitialItemsConfig, ItemsConfig, ItemType } from '../../../configs/item-config';
import { BoardState, CellState } from '../../../constants';
import { sample } from '../../../utils/array/sample';
import { shuffle } from '../../../utils/array/shuffle';
import {
  bunchClusters,
  findClustersByType,
  getBunchOfCell,
  getCellsFromBunch,
  makeCluster
} from '../../../utils/cluster';
import { clusterFilter } from '../../../utils/cluster/filters';
import { ObservableModel } from '../../observable-model';
import { CellModel } from './cell-model';
import { ItemModel } from './item-model';

export function getMergeItems() {
  return { main: 'sliced_tomato', second: 'potato', third: 'carrot' };
}

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');
    this._cells = null;
    this._state = BoardState.Unknown;
    this._matchLength = 2;
    this._clusters = [];
    this._bunches = [];
    this._mergeEpicenter = null;
    this._mergeCount = 0;
    this._mergedItem = null;
    this._pairs = null;
    this._transfer = null;
    this.makeObservable('_cells', '_state', '_mergeCount', '_mergedItem');
  }

  get cells() {
    return this._cells;
  }

  get mergeEpicenter() {
    return this._mergeEpicenter;
  }

  get mergeCount() {
    return this._mergeCount;
  }

  get state() {
    return this._state;
  }

  get mergedItem() {
    return this._mergedItem;
  }

  set mergedItem(value) {
    this._mergedItem = value;
  }

  set mergeCount(value) {
    this._mergeCount = value;
  }

  get transfer() {
    return this._transfer;
  }

  setState(state) {
    this._state = state;
  }

  updatePairs() {
    this._pairs = [];

    this._cells.forEach((col, x) => {
      col.forEach((cell, y) => {
        let left;
        let right;
        let bottom;
        let top;

        if (cell.item) {
          bottom = col[y - 1];
          top = col[y + 1];

          if (x - 1 >= 0 && x + 1 < this._cells.length) {
            left = this._cells[x - 1][y];
            right = this._cells[x + 1][y];
          }

          this._pairs.push(
            this.getPair(cell, bottom),
            this.getPair(cell, top),
            this.getPair(cell, left),
            this.getPair(cell, right)
          );
        }
      });
    });

    this._pairs = this._pairs.filter(p => p);
  }

  // eslint-disable-next-line class-methods-use-this
  getPair(cell1, cell2) {
    if (cell1 && cell2 && cell1.item && cell2.item && cell1.item.type === cell2.item.type) {
      return [cell1.item.uuid, cell2.item.uuid];
    }

    return null;
  }

  getEnabledItems() {
    const items = [];
    for (let i = 0; i < this._cells.length; i += 1) {
      items.push(...this._cells[i].filter(c => c && c.item && c.item.enabled).map(c => c.item));
    }

    return items;
  }

  getHintItems() {
    const items = [];
    items.push(...this.getItemsByType(ItemType.egg), ...this.getItemsByType(ItemType.small));
    return items.length ? items : this.getEnabledItems();
  }

  getItemsToMerge() {
    return [...this.getItemsByType(ItemType.egg), ...this.getItemsByType(ItemType.small)].map(i => i.uuid);

    // const enabledPairs = this._pairs.filter(p => {
    //   const item1 = this.getItem(p[0]);
    //   const item2 = this.getItem(p[1]);

    //   return item1.enabled && item2.enabled;
    // });

    // if (enabledPairs.length) {
    //   const pair = sample(enabledPairs);
    //   const { type } = this.getItem(pair[0]);
    //   const items = this.getItemsByType(type);
    //   const item = items.filter(i => i.uuid !== pair[0] && i.uuid !== pair[1])[0];

    //   return [item.uuid, pair[0]];
    // }

    // const { type } = sample(this.getEnabledItems());
    // return this.getItemsByType(type).map(i => i.uuid);
  }

  getEmptyCells() {
    const cells = [];
    for (let i = 0; i < this._cells.length; i += 1) {
      cells.push(...this._cells[i].filter(c => c && c.isEmpty === true));
    }

    return cells;
  }

  getCellsByState(state) {
    const cells = [];
    for (let i = 0; i < this._cells.length; i += 1) {
      cells.push(...this._cells[i].filter(c => c && c.state === state));
    }
    return cells;
  }

  getItem(itemUuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.item && c.item.uuid === itemUuid);
      if (cell) {
        const { item } = cell;
        if (item) {
          return item;
        }
      }
    }

    return null;
  }

  getCell(uuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.uuid === uuid);
      if (cell) {
        return cell;
      }
    }

    return null;
  }

  getCellByItemUuid(uuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.item && c.item.uuid === uuid);
      if (cell) {
        return cell;
      }
    }

    return null;
  }

  getItemsByType(type) {
    const items = [];
    for (let i = 0; i < this._cells.length; i += 1) {
      const row = this._cells[i].filter(c => c && c.item && c.item.type === type).map(c => c.item);
      items.push(...row);
    }

    return items;
  }

  gainNectar(animalCell, flowerCell) {
    this._transfer = { cellFrom: animalCell, cellTo: flowerCell };
    this.setState(BoardState.StartNectarGainSequence);
  }

  gainNectarComplete() {
    this.setState(BoardState.Idle);
    const animal = this.getItemsByType(ItemType.big)[0];
    const flower = this.getItemsByType(ItemType.flower)[0];
    animal.enabled = false;
    flower.enabled = false;
    const flowerCell = this.getCellByItemUuid(flower.uuid);

    const emptyNeighbors = this._getNeighbors(flowerCell).filter(c => c && c.isEmpty);
    shuffle(emptyNeighbors);
    const newCells = sample(emptyNeighbors);

    this._mergeEpicenter = flowerCell;
    const itemModel = new ItemModel(ItemType.nectar);
    newCells.setItem(itemModel);
    newCells.setState(CellState.Fill);
    this.setState(BoardState.ThrowItems);
  }

  clearClouds() {
    const nectar = this.getItemsByType(ItemType.nectar)[0];
    const nectarCell = this.getCellByItemUuid(nectar.uuid);

    nectar.activate = true;
    this._mergeEpicenter = nectarCell;

    this.setState(BoardState.StartClearCloudsSequence);
  }

  creationComplete(uuid) {
    const cell = this.getCell(uuid);
    cell.setState(CellState.Idle);

    if (this.getCellsByState(CellState.Fill).length === 0) {
      if (cell.item.type === getMergeItems().small) {
        this.setState(BoardState.Idle);
      } else {
        this.setState(BoardState.Idle);
      }
    }
  }

  actionDone(from, to, item) {
    this.setState(BoardState.ActionDone);
    if (to.isEmpty) {
      this._moveItemTo(from, to, item);
    } else if (from === to) {
      this._moveItemTo(from, to, item);
    } else if (!to.enabled) {
      this._moveItemTo(from, from, item);
    } else {
      from.removeItem();
      from.setState(CellState.Idle);

      if (to.item.type === item.type) {
        this._updateClustersAndBunches(this._sameTypeClusterFinder, this, to, item.type);

        if (to.item.uuid !== item.uuid) {
          const cellsItem = [];
          this._cells.forEach(cells => {
            const cs = cells.filter(c => c && c.item && c.item.type === item.type);

            cellsItem.push(...cs);
          });
          this.mergeItems(cellsItem, item, to);
        } else if (this._bunches.length) {
          const cellsOfBunch = getCellsFromBunch(this._bunches[0], this._cells);
          if (cellsOfBunch[0].item.type === item.type && cellsOfBunch[0].item.uuid !== item.uuid) {
            this.mergeItems(cellsOfBunch, item, to);
          } else {
            this._shiftItems(from, to, item);
          }
        } else {
          if (this._mergeCount === 0) {
            this._moveItemTo(from, from, item);
          } else {
            this._shiftItems(from, to, item);
          }
        }
      } else {
        if (this._mergeCount === 0) {
          this._moveItemTo(from, from, item);
        } else {
          this._shiftItems(from, to, item);
        }
      }
    }
  }

  mergeItems(cellsToMerge, mergeItem, epicenter) {
    this._mergeEpicenter = epicenter;
    mergeItem.remove = true;

    cellsToMerge.forEach(c => {
      c.setState(CellState.Merge);
    });
    this.setState(BoardState.MergeStart);
  }

  checkForMatch(cellUuid, itemUuid) {
    const to = this.getCell(cellUuid);
    const draggingItem = this.getItem(itemUuid);

    this._updateClustersAndBunches(this._sameTypeClusterFinder, this, to, draggingItem.type);

    if (this._bunches.length) {
      const cells = getCellsFromBunch(this._bunches[0], this._cells);

      if (to.item.type === draggingItem.type) {
        const cellsToHighlight = this.getItemsByType(draggingItem.type)
          .filter(i => i.uuid !== draggingItem.uuid)
          .map(i => this.getCellByItemUuid(i.uuid));

        this.startCellsHighlight(cellsToHighlight);
        this.setCellsMagnet(cells, to);

        // draggingItem.hide = true;
      }
    } else {
      this.stopCellsHighlight();
      draggingItem.hide = false;
    }
  }

  startCellsHighlight(cells = this.getCellsByState(CellState.Idle)) {
    cells.forEach(cell => {
      if (cell.item) cell.item.highlight = true;
    });
  }

  stopCellsHighlight(cells = this.getCellsByState(CellState.Idle)) {
    cells.forEach(cell => {
      if (cell.item) {
        cell.item.highlight = false;
        cell.item.magnet = null;
      }
    });
  }

  // eslint-disable-next-line class-methods-use-this
  setCellsMagnet(cells, to) {
    cells.forEach(c => {
      if (c.item) {
        c.item.magnet = to.uuid;
      }
    });
  }

  // eslint-disable-next-line consistent-return
  checkMergeAbleItems(cellUuid, itemUuid) {
    const draggingItem = this.getItem(itemUuid);
    const to = this.getCell(cellUuid);
    return to.item.type === draggingItem.type && to.item !== draggingItem;
  }

  initialize() {
    this.initializeCells();
    this.createInitialItems();
  }

  initializeCells() {
    const rows = [];
    for (let i = 0; i < 4; i += 1) {
      const row = [];

      for (let j = 0; j < 5; j += 1) {
        const cell = new CellModel(i, j);
        cell.setState(CellState.Idle);
        row.push(cell);
      }

      rows.push(row);
    }

    this._cells = rows;
  }

  createInitialItems() {
    const itemsConfig = getInitialItemsConfig();
    itemsConfig.forEach(item => {
      const { key, pos } = item;
      pos.forEach(p => {
        const { row, col } = p;
        const cell = this._cells[row][col];

        const itemModel = new ItemModel(key);
        cell.setItem(itemModel);
        cell.setState(CellState.Fill);
      });
    });

    this.setState(BoardState.AddItems);
  }

  mergeComplete(uuid) {
    const cell = this.getCell(uuid);
    const { type: mergedItemType } = cell.item;
    cell.item.remove = true;
    cell.removeItem();
    cell.setState(CellState.Idle);

    const mergeCells = this.getCellsByState(CellState.Merge);
    if (!mergeCells.length) {
      this.setState(BoardState.MergeComplete);
      const epicenterCell = this.getCell(this.mergeEpicenter.uuid);

      const { type, count } = ItemsConfig[mergedItemType].next;
      const neighbors = this._getNeighbors(epicenterCell);

      const emptyNeighbors = neighbors.filter(c => c && c.isEmpty);
      shuffle(emptyNeighbors);
      const newCells = [...emptyNeighbors.slice(0, count - 1), epicenterCell];

      newCells.forEach(c => {
        const item = new ItemModel(type);
        c.setItem(item);
        c.setState(CellState.Fill);
        this.setState(BoardState.ThrowItems);
      });
    }
  }

  moveComplete(uuid) {
    const cell = this.getCell(uuid);
    cell.setState(CellState.Idle);

    const moveCells = this.getCellsByState(CellState.Move);
    if (!moveCells.length) {
      this.setState(BoardState.Idle);
    }
  }

  _simpleClusterFinder(cell, matchLength = this._matchLength) {
    const { type } = cell.item;
    const c = findClustersByType(this._cells, matchLength, clusterFilter, clusterFilter, type);
    const b = bunchClusters(c);
    const clusters = getBunchOfCell(cell, b);
    const bunches = bunchClusters(clusters);
    return { clusters, bunches };
  }

  _sameTypeClusterFinder(cell, type) {
    const { row: powerUpRow, col: powerUpCol } = cell;

    const powerUpCluster1 = makeCluster(powerUpCol, powerUpRow, 1, true);
    const powerUpCluster2 = makeCluster(powerUpCol, powerUpRow, 1, false);

    const clusters = findClustersByType(this._cells, 1, clusterFilter, clusterFilter, type);
    clusters.push(powerUpCluster1, powerUpCluster2);
    const bunches = bunchClusters(clusters);

    return { clusters, bunches };
  }

  _updateClustersAndBunches(finder, context, ...args) {
    const { clusters, bunches } = finder.call(context, ...args);
    this._resetClustersAndBunches();

    this._clusters.push(...clusters);
    this._bunches.push(...bunches);
  }

  _resetClustersAndBunches() {
    this._clusters.length = 0;
    this._bunches.length = 0;
  }

  _moveItemTo(from, to, item) {
    from.removeItem();
    to.setItem(item);

    from.setState(CellState.Idle);
    to.setState(CellState.Move);

    this.setState(BoardState.Move);
  }

  _shiftItems(from, to, item) {
    const emptyNeighbors = this._getNeighbors(to).filter(c => c.isEmpty);

    if (emptyNeighbors.length) {
      const cell = sample(emptyNeighbors);
      from.removeItem();
      from.setState(CellState.Idle);
      this._replaceItems(cell, to, item);
    } else {
      this._replaceItems(from, to, item);
    }
  }

  _replaceItems(from, to, itemFrom) {
    const { item: itemTo } = to;

    from.removeItem();
    to.removeItem();
    to.setItem(itemFrom);
    from.setItem(itemTo);

    from.setState(CellState.Move);
    to.setState(CellState.Move);

    this.setState(BoardState.Move);
  }

  _getNeighbors(cell) {
    const { row, col } = cell;

    const up = this.cells[row - 1] && this.cells[row - 1][col];
    const upLeft = this.cells[row - 1] && this.cells[row - 1][col - 1];
    const upRight = this.cells[row - 1] && this.cells[row - 1][col + 1];
    const left = this.cells[row][col - 1];
    const right = this.cells[row][col + 1];
    const bottom = this.cells[row + 1] && this.cells[row + 1][col];
    const bottomLeft = this.cells[row + 1] && this.cells[row + 1][col - 1];
    const bottomRight = this.cells[row + 1] && this.cells[row + 1][col + 1];
    const neighbors = [upLeft, up, upRight, right, bottomRight, bottom, bottomLeft, left];

    return neighbors.filter(c => c);
  }
}
