/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import {
  getBackgroundViewImageConfig,
  getCircleImageConfig,
  getGhostImageConfig,
  getGlowBallImageConfig
} from '../../../configs/image-configs';
import { getCloudsConfig, ItemType } from '../../../configs/item-config';
import { getCircleEmitterConfig, getNectarEmitterConfig } from '../../../configs/particles-configs';
import { BoardState, CellState, ISO_ANGLE } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { delayRunnable, makeEmitter, makeImage } from '../../../utils';
import { sample } from '../../../utils/array/sample';
import { Container } from '../../../utils/container';
import { randomInt } from '../../../utils/number/random-int';
import { CloudComponent } from '../items/cloud-component';
import { CellView, CELL_CENTER_X, CELL_CENTER_Y } from './cell-view';
import { ItemView } from './item-view';

export class BoardView extends Container {
  constructor() {
    super();

    this._bg = null;
    this._cells = [];
    this._items = [];
    this._itemsToRemove = [];
    this._pos = [];
    this.cellsCreated = new Phaser.Signal();
    this._cloudsDisappeared = 0;

    lego.event
      .on(ModelEvents.BoardModel.CellsUpdate, this._onCellsUpdate, this)
      .on(ModelEvents.CellModel.StateUpdate, this._onCellModelStateUpdate, this)
      .on(ModelEvents.BoardModel.StateUpdate, this._onBoardModelStateUpdate, this)
      .on(ModelEvents.ItemModel.HighlightUpdate, this._onItemModelHighlightUpdate, this)
      .on(ModelEvents.ItemModel.ActivateUpdate, this._onItemModelActivateUpdate, this)
      .on(ModelEvents.ItemModel.RemoveUpdate, this._onItemModelRemoveUpdate, this)
      .on(ModelEvents.ItemModel.HideUpdate, this._onItemModelHideUpdate, this)
      .on(ModelEvents.ItemModel.MagnetUpdate, this._onItemModelMagnetUpdate, this)
      .on(ModelEvents.ItemModel.AlertUpdate, this._onItemModelAlertUpdate, this)
      .on(ModelEvents.ItemModel.EnabledUpdate, this._onItemEnabledUpdate, this);

    this._build();
  }

  // @ts-ignore
  get name() {
    return 'BoardView';
  }

  _onItemModelHighlightUpdate(value, prevValue, uuid) {
    const item = this.getItem(uuid);
    value ? item.startHighlight() : item.stopHighlight();
  }

  _onItemModelActivateUpdate(value, prevValue, uuid) {
    const item = this.getItem(uuid);
    value ? item.startActivate() : item.stopActivate();
  }

  _onItemModelHideUpdate(value, prevValue, uuid) {
    const item = this.getItem(uuid);
    value ? item.hide() : item.show();
  }

  _onItemModelRemoveUpdate(value, prevValue, uuid) {
    const item = this.getItem(uuid);

    if (value) {
      this._itemsToRemove.push(item);
      item.destroy();
    }
  }

  _onItemModelMagnetUpdate(value, prevValue, uuid) {
    const item = this.getItem(uuid);

    if (value) {
      const cellView = this.getCell(value);
      const { x: cX, y: cY } = cellView;
      const { x: iX, y: iY } = item;

      const dY = cY - iY !== 0 ? (cY - iY) / Math.abs(cY - iY) : 0;
      const dX = cX - iX !== 0 ? (cX - iX) / Math.abs(cX - iX) : 0;

      item.startMagnet(dX, dY);
    } else {
      item.stopMagnet();
    }
  }

  _onItemModelAlertUpdate(value, prevValue, uuid) {
    const item = this.getItem(uuid);
    value ? item.startAlert() : item.stopAlert();
  }

  _onItemEnabledUpdate(value, prevValue, uuid) {
    const item = this.getItem(uuid);
    value ? item.makeDraggable() : item.removeDraggable();
    this._updateItemsOrder();
  }

  getCell(uuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.uuid === uuid);
      if (cell) {
        return cell;
      }
    }

    return null;
  }

  getItem(uuid) {
    return this._items.find(item => item.uuid === uuid);
  }

  _onCellsUpdate(cells) {
    this._buildCells(cells);
    this.cellsCreated.dispatch();
  }

  _onCellModelStateUpdate(state, prevState, uuid) {
    if (!this._cells.length) {
      return;
    }

    switch (state) {
      case CellState.Reject:
        this._rejectDrag(uuid);
        break;
      case CellState.Idle:
        this._updateItemsOrder();
        break;

      default:
        break;
    }
  }

  _onBoardModelStateUpdate(state) {
    this._stopListenDrag();
    switch (state) {
      case BoardState.Idle:
        this._startListenDrag();
        break;
      case BoardState.AddItems:
        this._addNewItems();
        break;
      case BoardState.ThrowItems:
        this._throwNewItems();
        break;
      case BoardState.Move:
        this._moveItems();
        break;
      case BoardState.MergeStart:
        this._mergeItems();
        break;
      case BoardState.StartNectarGainSequence:
        this._startNectarGainSequence();
        break;
      case BoardState.StartClearCloudsSequence:
        this._startClearCloudsSequence();
        break;

      default:
        break;
    }
  }

  _makeItem(itemModel) {
    const { uuid, type } = itemModel;
    const itemView = new ItemView(uuid, type);
    this._cellsContainer.addChild(itemView);
    this._items.push(itemView);
    itemView.dragStart.add(this._onItemDragStart, this);
    itemView.dragStop.add(this._onItemDragStop, this);
    itemView.dragUpdate.add(this._onItemDragUpdate, this);
    itemView.onClick.add(this._onItemClick, this);

    return itemView;
  }

  _onItemDragStop(itemView) {
    lego.event.emit(ViewEvents.BoardView.ItemIsInBounds, itemView);

    let cell = null;
    const itemModel = store.game.boardModel.getItem(itemView.uuid);
    const maxInteractionCell = this._calculateInteraction(itemView);

    if (maxInteractionCell) {
      const { uuid } = maxInteractionCell;
      cell = store.game.boardModel.getCell(uuid);
    }

    lego.event.emit(ViewEvents.BoardView.DragStop, cell, itemModel);
  }

  _onItemDragStart(itemView) {
    const cell = store.game.boardModel.getCellByItemUuid(itemView.uuid);
    lego.event.emit(ViewEvents.BoardView.DragStart, cell, itemView);
  }

  _onItemDragUpdate(itemView) {
    const maxInteractionCell = this._calculateInteraction(itemView);

    if (maxInteractionCell) {
      const { item } = store.game.boardModel.getCell(maxInteractionCell.uuid);
      if (item && item.uuid !== itemView.uuid) {
        const { enabled } = item;
        if (enabled) {
          lego.event.emit(ViewEvents.BoardView.ItemIsInBounds, itemView);
          lego.event.emit(ViewEvents.BoardView.DragOverItem, maxInteractionCell.uuid, itemView.uuid);
        } else {
          lego.event.emit(ViewEvents.BoardView.ItemIsOutOfBounds, itemView);
        }
      } else {
        lego.event.emit(ViewEvents.BoardView.ItemIsInBounds, itemView);
        lego.event.emit(ViewEvents.BoardView.DragOverEmptyCell, maxInteractionCell.uuid, itemView.uuid);
      }
    } else {
      lego.event.emit(ViewEvents.BoardView.ItemIsOutOfBounds, itemView);
    }
  }

  _onItemClick(itemView) {
    const cell = store.game.boardModel.getCellByItemUuid(itemView.uuid);
    const itemModel = store.game.boardModel.getItem(itemView.uuid);
    lego.event.emit(ViewEvents.BoardView.ItemClicked, cell, itemModel);
  }

  _startNectarGainSequence() {
    const { transfer } = store.game.boardModel;
    const { cellFrom, cellTo } = transfer;
    const { uuid: animalUuid } = cellFrom.item;
    // const { uuid: flowerUuid } = cellTo.item;

    const animal = this.getItem(animalUuid);
    this._updateItemsOrder();

    const { x: originX, y: originY } = this.getCell(cellTo.uuid);
    const x = originX + CELL_CENTER_X;
    const y = originY + CELL_CENTER_Y;
    const tw = this.game.add.tween(animal).to({ x, y }, 1000, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;
    tw.onComplete.add(() => {
      lego.event.emit(ViewEvents.BoardView.GainNectarSequenceComplete);
      this._animalRandomMovementStart(animal);
    });
  }

  _startClearCloudsSequence() {
    const { uuid } = store.game.boardModel.getItemsByType(ItemType.nectar)[0];
    const nectar = this.getItem(uuid);
    const { x: fromX, y: fromY } = nectar;

    this._plaFXAnimation(fromX, fromY, getNectarEmitterConfig);

    this._clouds.forEach(cloud => {
      const { x: toX, y: toY } = this._cellsContainer.toLocal(cloud.position, this);
      const fx = makeImage(getGlowBallImageConfig());
      fx.position.set(fromX, fromY - 20);
      fx.alpha = 0;
      this._cellsContainer.addChild(fx);

      const delay = randomInt(100, 1000);
      const tw1 = this.game.add.tween(fx).to({ alpha: 1 }, 300, Phaser.Easing.Cubic.InOut, true, delay);
      tw1.universal = true;
      const tw = this.game.add
        .tween(fx)
        .to(
          { x: [toX + (toX - fromX) / 3, toX], y: [toY - 200, toY] },
          800,
          Phaser.Easing.Sinusoidal.InOut,
          true,
          delay
        );
      tw.interpolation(Phaser.Math.bezierInterpolation);
      tw.universal = true;
      tw.onComplete.add(() => {
        fx.destroy();
        this._hideCloud(cloud);
        this._makeExplosionFx(fx.x, fx.y);
      });
    });
  }

  _makeExplosionFx(x, y) {
    const circle = makeImage(getCircleImageConfig());
    circle.alpha = 0.7;
    circle.scale.set(0);
    this._fxContainer.addChild(circle);
    circle.position.set(x, y - 20);

    this.game.add
      .tween(circle.scale)
      .to({ x: 5, y: 5 }, 200, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, true)
      .onComplete.add(() => {
        circle.alpha = 0;
        circle.destroy();
      });
  }

  _hideCloud(cloud) {
    this.game.tweens.removeFrom(cloud);
    const tw1 = this.game.add
      .tween(cloud)
      .to({ x: `${sample([-1, 1]) * randomInt(3, 50)}` }, 800, Phaser.Easing.Sinusoidal.InOut, true);
    tw1.universal = true;

    const tw = this.game.add.tween(cloud).to({ alpha: 0 }, 600, Phaser.Easing.Sinusoidal.InOut, true);
    tw.universal = true;
    tw.onComplete.add(() => {
      cloud.destroy();
      this._cloudsDisappeared += 1;

      if (this._cloudsDisappeared === this._clouds.length) {
        lego.event.emit(ViewEvents.BoardView.AllCloudsDisappeared);
      }
    });
  }

  _animalRandomMovementStart(animal) {
    if (!store.game) {
      return;
    }

    const emptyCells = store.game.boardModel.getEmptyCells();
    const { uuid } = sample(emptyCells);
    const to = this.getCell(uuid);

    const { x: originX, y: originY } = to;
    const x = originX + CELL_CENTER_X;
    const y = originY + CELL_CENTER_Y;

    animal.x - x > 0 ? animal.turnLeft() : animal.turnRight();

    const tw = this.game.add
      .tween(animal)
      .to({ x, y }, randomInt(1700, 2300), Phaser.Easing.Sinusoidal.InOut, true, 1000);
    tw.universal = true;
    tw.onComplete.add(() => {
      this._animalRandomMovementStart(animal);
    });
  }

  _mergeItems() {
    const cellsToMerge = store.game.boardModel.getCellsByState(CellState.Merge);
    const { mergeEpicenter } = store.game.boardModel;
    const epicenterView = this.getCell(mergeEpicenter.uuid);
    const { x: originX, y: originY } = epicenterView;
    const x = originX + CELL_CENTER_X;
    const y = originY + CELL_CENTER_Y;
    delayRunnable(100, () => {
      lego.event.emit(ViewEvents.BoardView.PlayFXAnimation);

      this._plaFXAnimation(x, y);
    });

    // this._plaFXAnimation(x, y);
    cellsToMerge.forEach(c => {
      const { uuid } = c.item;
      const itemView = this.getItem(uuid);

      this._setItemPosition(itemView, epicenterView).onComplete.add(() => {
        lego.event.emit(ViewEvents.BoardView.MergeComplete, c.uuid);

        // this.game.add
        //   .tween(itemView.scale)
        //   .to({ x: 0, y: 0 }, 500, Phaser.Easing.Circular.Out, true)
        //   .onComplete.add(() => {
        //   });
      });
    });
  }

  _plaFXAnimation(x, y, emitterConfig = getCircleEmitterConfig) {
    this._makeExplosionFx(x, y);
    this._sparkleEmitter = makeEmitter(emitterConfig.call(this, this._fxContainer, { x, y }));
  }

  _startListenDrag() {
    this._items.forEach(item => {
      const itemModel = store.game.boardModel.getItem(item.uuid);
      if (itemModel) {
        const { enabled } = itemModel;
        if (enabled) {
          item.makeDraggable();
        }
      }
    });
  }

  _stopListenDrag() {
    this._items.forEach(item => {
      const cellModel = store.game.boardModel.getCellByItemUuid(item.uuid);
      if (cellModel && cellModel.state !== CellState.Drag) {
        item.removeDraggable();
      }
    });
  }

  _moveItems() {
    const cellsWithItemsToMove = store.game.boardModel.getCellsByState(CellState.Move);
    cellsWithItemsToMove.forEach(cellModel => {
      const { item, uuid } = cellModel;
      const itemView = this.getItem(item.uuid);

      this._setItemPosition(itemView, this.getCell(uuid)).onComplete.add(() => {
        lego.event.emit(ViewEvents.BoardView.CellMoveComplete, uuid);
      });
    });
  }

  _addNewItems() {
    const cellsWithNewItems = store.game.boardModel.getCellsByState(CellState.Fill);
    cellsWithNewItems.forEach(cellModel => {
      const { item, uuid } = cellModel;
      const itemView = this._makeItem(item);

      this._setItemPosition(itemView, this.getCell(uuid), true);

      this.game.add.tween(itemView.scale).from({ x: 0, y: 0 }, 200, Phaser.Easing.Circular.Out, true);

      lego.event.emit(ViewEvents.BoardView.ItemsCreated, uuid);
    });
  }

  _throwNewItems() {
    const { uuid: epicenterUuid } = store.game.boardModel.mergeEpicenter;

    const cellsWithNewItems = store.game.boardModel.getCellsByState(CellState.Fill);

    cellsWithNewItems.forEach(cellModel => {
      const { item, uuid } = cellModel;
      const itemView = this._makeItem(item);

      const { x: originX, y: originY } = this.getCell(epicenterUuid);
      const x = originX + CELL_CENTER_X;
      const y = originY + CELL_CENTER_Y;

      this._setItemPosition(itemView, this.getCell(uuid), true);
      itemView.scale.set(0);
      this.game.add.tween(itemView.scale).to({ x: 1, y: 1 }, 500, Phaser.Easing.Circular.Out, true);

      const twn = this.game.add.tween(itemView).from({ x, y }, 500, Phaser.Easing.Sinusoidal.InOut, true);
      twn.universal = true;
      lego.event.emit(ViewEvents.BoardView.ItemsCreated, uuid);
    });
  }

  _rejectDrag(cellUuid) {
    const { item } = store.game.boardModel.getCell(cellUuid);
    const itemView = this.getItem(item.uuid);
    const cellView = this.getCell(cellUuid);

    this._setItemPosition(itemView, cellView).onComplete.add(() => {
      lego.event.emit(ViewEvents.BoardView.RejectComplete, cellUuid);
    });
  }

  _setItemPosition(item, cell, force = false) {
    const { x: originX, y: originY } = cell;
    const x = originX + CELL_CENTER_X;
    const y = originY + CELL_CENTER_Y;
    let tween = null;

    if (force) {
      item.position.set(x, y);
    } else {
      tween = this.game.add.tween(item).to({ x, y }, 200, Phaser.Easing.power4, true);
    }

    return tween;
  }

  _buildCells(cells) {
    cells.forEach(r => {
      const viewRow = [];

      r.forEach(c => {
        const { row, col } = c;
        const cell = new CellView(row, col, c.uuid);

        cell.x = 78.5 * col - 57 * row;
        cell.y = 102.5 * row;
        this._cellsContainer.addChild(cell);
        viewRow.push(cell);
      });

      this._cells.push(viewRow);
    });
  }

  _build() {
    this._buildBg();
    this._buildCellsContainer();
    this._buildClouds();
    this._buildGhost();
    this._buildFxContainer();
  }

  _buildBg() {
    this._bg = makeImage(getBackgroundViewImageConfig('bg/bg.jpg'));
    this._bg.inputEnabled = true;
    this._bg.input.priorityID = 0;
    this._bg.name = 'Background';
    this.addChild(this._bg);
  }

  _buildCellsContainer() {
    this._cellsContainer = new Container();
    this.addChild(this._cellsContainer);
    this._cellsContainer.angle = ISO_ANGLE;
    this._cellsContainer.position.set(142, -176);
  }

  _buildFxContainer() {
    this._fxContainer = new Container();
    this._fxContainer.position.set(142, -176);
    this._fxContainer.angle = ISO_ANGLE;
    this.addChild(this._fxContainer);
  }

  _buildGhost() {
    const ghost = makeImage(getGhostImageConfig());
    ghost.anchor.set(0.5, 1);
    ghost.position.set(330, -310);

    this.addChild(ghost);

    const tw = this.game.add
      .tween(ghost.scale)
      .to({ x: 1.2, y: 1.2 }, randomInt(2500, 4000), Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
    tw.universal = true;
  }

  _buildClouds() {
    const config = getCloudsConfig();
    this._clouds = [];
    config.forEach(c => {
      const { scale, position, layerOrder } = c;

      const cloud = new CloudComponent(layerOrder);
      this._clouds.push(cloud);
      cloud.scale.set(scale, Math.abs(scale));
      // @ts-ignore
      cloud.position.copyFrom(position);
      this.addChild(cloud);
      const tw = this.game.add
        .tween(cloud)
        .to(
          { x: `${sample([-1, 1]) * randomInt(5, 10)}` },
          randomInt(1700, 2000),
          Phaser.Easing.Sinusoidal.InOut,
          true,
          randomInt(500, 800),
          -1,
          true
        );
      tw.universal = true;
    });
  }

  _updateItemsOrder() {
    const bigAnimals = store.game.boardModel.getItemsByType(ItemType.big);

    store.game.boardModel.cells.forEach(row => {
      row.forEach(cell => {
        const { item } = cell;
        if (item) {
          const itemView = this.getItem(item.uuid);
          if (itemView) this.bringToTop(itemView);
        }
      });
    });

    if (bigAnimals.length) {
      const { uuid } = bigAnimals[0];
      const bigAnimal = this.getItem(uuid);
      bigAnimal && bigAnimal.bringToTop();
    }

    this._clouds.forEach(c => {
      if (c.layerOrder === 'front') {
        this.bringToTop(c);
      }
    });

    this.bringToTop(this._fxContainer);
  }

  _calculateInteraction(itemView) {
    let maxInteraction = new Phaser.Rectangle();
    let maxInteractionCell;

    for (let i = 0; i < this._cells.length; i += 1) {
      const row = this._cells[i];
      for (let j = 0; j < row.length; j += 1) {
        const c = row[j];

        const r = c.getBounds().intersection(itemView.getBounds());
        const { boardModel } = store.game;
        const { item } = boardModel.getCell(c.uuid);

        if (
          r.volume > 0 &&
          item &&
          item.type === boardModel.getItem(itemView.uuid).type &&
          boardModel.checkMergeAbleItems(c.uuid, itemView.uuid)
        ) {
          maxInteraction = r;
          maxInteractionCell = c;
          return maxInteractionCell;
        }

        if (r.volume > maxInteraction.volume) {
          maxInteraction = r;
          maxInteractionCell = c;
        }
      }
    }

    return maxInteractionCell;
  }
}
