/* eslint-disable class-methods-use-this */
import { getCloudImageConfig } from '../../../configs/image-configs';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class CloudComponent extends Container {
  constructor(layerOrder) {
    super();
    this._layerOrder = layerOrder;
    this._build();
  }

  get layerOrder() {
    return this._layerOrder;
  }

  _build() {
    const img = makeImage(getCloudImageConfig());
    this.addChild((this._img = img));
  }
}
