import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getTutorialSequenceGridLandscapeConfig() {
  return {
    // debug: { color: 0xd95777 },
    name: 'tutorial_sequence',
    cells: [
      {
        name: 'charector',
        bounds: { x: 0, y: 0.57, width: 0.33, height: 0.43 },
        padding: { y: 0, height: 0.96 },
        align: CellAlign.RightCenter
        // scale: CellScale.
      },
      {
        name: 'bg',
        bounds: { x: 0, y: 0.775, width: 1, height: 0.225 },
        scale: CellScale.Fill
      },
      {
        name: 'content',
        bounds: { x: 0, y: 0.775, width: 1, height: 0.225 },
        padding: { x: 0.35, width: 0.5 }
      }
    ]
  };
}

export function getTutorialSequenceGridPortraitConfig() {
  const characterConfig =
    CI_API.Globals.PARAMS.tutorial_character === 'grandma'
      ? {
          name: 'charector',
          bounds: { x: 0.01, y: 0.76, width: 0.38, height: 0.24 },
          padding: { y: 0, height: 0.96 }
        }
      : {
          name: 'charector',
          bounds: { x: -0.1, y: 0.76, width: 0.48, height: 0.24 },
          padding: { y: 0, height: 0.96 },
          align: CellAlign.RightBottom
        };

  return {
    // debug: { color: 0xd95777 },
    name: 'tutorial_sequence',
    cells: [
      characterConfig,
      {
        name: 'bg',
        bounds: { x: 0, y: 0.85, width: 1, height: 0.15 },
        scale: CellScale.Fill
      },
      {
        name: 'content',
        bounds: { x: 0, y: 0.85, width: 1, height: 0.15 },
        padding: { x: 0.35, width: 0.57 }
      }
    ]
  };
}
