import { getGameBounds } from '../utils';

const sparkle = 'sparkle-';
const ctaLiens = 'ctaLiens';
const cta = 'cta';
const mergeItem = 'merge_item';

function getCircleConfettiParticlesConfig() {
  return {
    key: mergeItem,
    data: {
      lifespan: 700,
      frame: ['fx/fx_star.png'],
      scale: {
        initial: 0,
        value: 2.5,
        control: [
          { x: 0, y: 0.25 },
          { x: 0.3, y: 0.75 },
          { x: 1, y: 0 }
        ]
      },
      alpha: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      vy: { min: -5, max: 5 },
      vx: { min: -5, max: 5 }
    }
  };
}

export function getSparkleParticlesConfig() {
  return {
    key: sparkle,
    data: {
      lifespan: 600,
      frame: ['fx/fx_star.png'],
      scale: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      alpha: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      vy: { min: -2, max: 2 },
      vx: { min: -2, max: 2 },
      rotation: { delta: { min: -10, max: 10 } }
    }
  };
}

export function getMergedEffectSparkleEmitterConfig(parent, view) {
  const { x, y } = view;
  return {
    key: cta,
    parent,
    x,
    y,
    force: { x: 0, y: 0 },
    config: {
      zone: CI_API.game.particleStorm.createCircleZone(100),
      total: 15,
      repeat: -1
    }
  };
}

export function getCtaParticlesConfig() {
  return {
    key: ctaLiens,
    data: {
      frame: ['particles/leave_1.png', 'particles/leave_3.png', 'particles/leave_2.png'],
      lifespan: 7000,
      vx: { min: -2, max: 2 },
      vy: {
        value: 0,
        control: [
          { x: 0, y: 1 },
          { x: 0.3, y: 1 },
          { x: 0.9, y: 0.01 },
          { x: 1, y: 0 }
        ]
      },
      scale: { min: 0.7, max: 1.2 },
      // rotation: { delta: { min: -5, max: 5 } }
      rotation: {
        initial: -90,
        value: 180,
        control: [
          { x: 0, y: 0 },
          { x: 0.2, y: 0.5 },
          { x: 0.4, y: 1 },
          { x: 0.6, y: 0.5 },
          { x: 1, y: 0 }
        ]
      }
    }
  };
}

export const particles = [getCircleConfettiParticlesConfig(), getSparkleParticlesConfig(), getCtaParticlesConfig()];

export function getPCTASparkleEmitterConfig(parent, view) {
  const { x, y, width, height } = view;
  return {
    key: sparkle,
    parent,
    x: x - width / 2,
    y: y - height / 2,
    force: { x: 0, y: 0 },
    config: {
      zone: CI_API.game.particleStorm.createRectangleZone(width, height),
      total: 10,
      repeat: -1
    }
  };
}

export function getCtaEmitterConfig(parent) {
  const { x, y, width, height } = getGameBounds();
  return {
    key: ctaLiens,
    parent,
    x,
    y,
    force: { x: 0, y: 0.05 },
    config: {
      zone: CI_API.game.particleStorm.createLineZone(0, -200, Math.max(width, height), -200),
      total: 5,
      repeat: -1,
      frequency: 400
    }
  };
}

