import { CellState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class CellModel extends ObservableModel {
  constructor(row, col) {
    super('CellModel');

    this._col = col;
    this._row = row;
    this._item = null;
    this._state = CellState.Unknown;

    this.makeObservable('_item', '_state');
  }

  get enabled() {
    const enabled = this._item && this._item.enabled;
    return enabled;
  }

  get isEmpty() {
    const isEmpty = !this._item;
    return isEmpty;
  }

  get state() {
    return this._state;
  }

  get item() {
    return this._item;
  }

  get row() {
    return this._row;
  }

  get col() {
    return this._col;
  }

  setItem(item) {
    this._item = item;
  }

  removeItem() {
    this._item = null;
  }

  setState(state) {
    this._state = state;
  }
}
