export class Rhombus extends Phaser.Polygon {
  constructor(x, y, width, height, angleDeg = -90) {
    super();
    this._x = x;
    this._y = y;
    this._width = width;
    this._height = height;
    const startX = x - width / 2;
    const startY = y - height / 2;
    const angleRad = Phaser.Math.degToRad(angleDeg);

    this.setTo(
      new Phaser.Point(startX, startY),
      new Phaser.Point(startX + width * Math.cos(angleRad), startY + width * Math.sin(angleRad)),
      new Phaser.Point(startX + width * Math.cos(angleRad) + height, startY + width * Math.sin(angleRad)),
      new Phaser.Point(startX + height, startY)
    );
  }

  get x() {
    return this._x;
  }

  set x(value) {
    const offsetX = value - this.x;
    if (offsetX === 0) {
      return;
    }
    this._offset(offsetX, 0);
    this._x = value;
  }

  get y() {
    return this._y;
  }

  set y(value) {
    const offsetY = value - this.y;
    if (offsetY === 0) {
      return;
    }
    this._offset(0, offsetY);
    this._y = value;
  }

  setPosition(x, y) {
    const offsetX = x - this.x;
    const offsetY = y - this.y;
    if (offsetX === 0 && offsetY === 0) {
      return this;
    }
    this._offset(offsetX, offsetY);
    this._x = x;
    this._y = y;
    return this;
  }

  get left() {
    return this.x;
  }

  set left(value) {
    this.x += value - this.left;
  }

  get right() {
    return this.x + this.width;
  }

  set right(value) {
    this.x += value - this.right;
  }

  get top() {
    return this.y;
  }

  set top(value) {
    this.y += value - this.top;
  }

  get bottom() {
    return this.y + this.height;
  }

  set bottom(value) {
    this.y += value - this.bottom;
  }

  get centerX() {
    return this.x + this.width / 2;
  }

  set centerX(value) {
    this.x += value - this.centerX;
  }

  get centerY() {
    return this.y + this.height / 2;
  }

  set centerY(value) {
    this.y += value - this.centerY;
  }

  get width() {
    return this._width;
  }

  get height() {
    return this._height;
  }

  isEmpty() {
    return this.width <= 0 || this.height <= 0;
  }

  getEdge(idx, line) {
    if (line === undefined) {
      // eslint-disable-next-line no-param-reassign
      line = new Phaser.Line();
    }
    const p0 = this.points[idx];
    const p1 = this.points[(idx + 1) % 4];
    // @ts-ignore
    line.setTo(p0.x, p0.y, p1.x, p1.y);
    return line;
  }

  getLineA(line) {
    return this.getEdge(0, line);
  }

  getLineB(line) {
    return this.getEdge(1, line);
  }

  getLineC(line) {
    return this.getEdge(2, line);
  }

  getLineD(line) {
    return this.getEdge(3, line);
  }

  _offset(x, y) {
    const { points } = this;
    let point;
    for (let i = 0, cnt = points.length; i < cnt; i += 1) {
      point = points[i];
      // @ts-ignore
      point.x += x;
      // @ts-ignore
      point.y += y;
    }
  }
}
