/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAContainerGridConfig } from '../../configs/grid-configs';
import { AdStatus } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { CTAView } from './cta-view';

export class CTAContainer extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return getCTAContainerGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this._build();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;
      default:
    }
  }

  _build() {
    super.build(this.getGridConfig());
    this._buildCta();
  }

  _buildCta() {
    this._cta = new CTAView();
    this.setChild('cta', this._cta);
    this._cta.alpha = 0;
    this._cta.show();
  }
}
