/**
 * Returns array with extreme points of cluster.
 * @param cluster
 * @returns {Array} points
 */
export const getVertices = cluster => {
  return [
    [cluster.row, cluster.column],
    cluster.horizontal
      ? [cluster.row, cluster.column + cluster.length - 1]
      : [cluster.row + cluster.length - 1, cluster.column]
  ];
};

/**
 * Check if clusters is crossing.
 * @param a first cluster
 * @param b second cluster
 * @returns bool
 */
export const isCrossingClusters = (a, b) => {
  if (!a || !b) {
    return false;
  }
  if (a.horizontal === b.horizontal) {
    return false;
  }
  const verticesA = getVertices(a);
  const verticesB = getVertices(b);

  return (
    (verticesB[0][1] >= verticesA[0][1] &&
      verticesB[1][1] <= verticesA[1][1] &&
      verticesA[0][0] >= verticesB[0][0] &&
      verticesA[1][0] <= verticesB[1][0]) ||
    (verticesA[0][1] >= verticesB[0][1] &&
      verticesA[1][1] <= verticesB[1][1] &&
      verticesB[0][0] >= verticesA[0][0] &&
      verticesB[1][0] <= verticesA[1][0])
  );
};

/**
 * Find clusters which belongs to a bunch.
 * @param {Array} clusters
 * @param {Array} bunch
 * @returns {Array} bunch of clusters
 */
export const findRelatedClusters = (clusters, bunch) => {
  const newBunch = [];
  bunch.forEach(cluster => {
    const clusterIndex = clusters.indexOf(cluster);
    if (clusterIndex !== -1) {
      newBunch.push(cluster);
      clusters.splice(clusterIndex, 1);
    }
    let index = clusters.length - 1;
    while (index >= 0) {
      const el = clusters[index];
      if (isCrossingClusters(cluster, el)) {
        newBunch.push(el);
        clusters.splice(index, 1);
      }
      index -= 1;
    }
  });
  if (newBunch.length > 0) {
    return [...newBunch, ...findRelatedClusters(clusters, newBunch)];
  }

  return newBunch;
};

/**
 * Grouped clusters into bunches.
 * @param {Array} rawClusters
 * @returns {Array} bunch of clusters
 */
export const bunchClusters = rawClusters => {
  const clusters = [...rawClusters];
  if (clusters.length < 1) {
    return [];
  }

  return [findRelatedClusters(clusters, [clusters[0]]), ...bunchClusters(clusters)];
};

/**
 * Check is cluster contains the cell.
 * @param cluster
 * @param cell
 * @returns bool - true if cluster contains cell, otherwise false
 */
export const doesClusterContainCell = (cluster, cell) => {
  const vertices = getVertices(cluster);
  const { row, col } = cell;

  if (cluster.horizontal) {
    return vertices[0][0] === row && vertices[0][1] <= col && vertices[1][1] >= col;
  }

  return vertices[0][1] === col && vertices[0][0] <= row && vertices[1][0] >= row;
};

/**
 * Return a bunch which contains the cell
 * @param cell to compere
 * @param bunches from where should looking for
 * @returns {Array} bunch
 */
export const getBunchOfCell = (cell, bunches) => {
  return (
    bunches.find(bunch => {
      return bunch.find(cluster => {
        return doesClusterContainCell(cluster, cell);
      });
    }) || []
  );
};

/**
 * Return cells of which consist the bunch
 * @param {Array} bunche
 * @param {Array} allCells
 * @returns {Array} array of cells
 */
export const getCellsFromBunch = (bunche, allCells) => {
  const cells = [];
  for (let i = 0; i < bunche.length; i += 1) {
    const cluster = bunche[i];
    let coffset = 0;
    let roffset = 0;
    for (let j = 0; j < cluster.length; j += 1) {
      const cell = allCells[cluster.row + roffset][cluster.column + coffset];
      if (!cells.find(c => c === cell)) cells.push(cell);
      if (cluster.horizontal) {
        coffset += 1;
      } else {
        roffset += 1;
      }
    }
  }

  return cells;
};

/**
 * Make cluster with given parameters
 * @param {number} column
 * @param {number} row
 * @param {number} length
 * @param {boolean} horizontal
 * @returns cluster
 */
export const makeCluster = (column, row, length, horizontal) => {
  return {
    column,
    row,
    length,
    horizontal
  };
};

/**
 * Make horizontal and vertical clusters on given points with length = 1
 * @param {Array} points
 * @returns clusters
 */
export const makeUnitClustersFromPoints = points => {
  const clusters = [];

  points.forEach(point => {
    const { col, row } = point;
    const hCluster = makeCluster(col, row, 1, true);
    const vCluster = makeCluster(col, row, 1, false);
    clusters.push(hCluster, vCluster);
  });

  return clusters;
};

/**
 * Find clusters with horizontal direction
 * @param {array} cells
 * @param {number} length matching length
 * @param {function} filter
 * @param {number} identity
 * @returns {Array} clusters
 */
export const findHorizontalClusters = (cells, length, filter, identity) => {
  const rows = cells.length;
  const columns = cells[0].length;
  const clusters = [];

  for (let i = 0; i < rows; i += 1) {
    let matchLength = 0;

    for (let j = 0; j < columns; j += 1) {
      if (filter(cells, i, j, identity)) {
        // Identify an item
        matchLength += 1;

        // Last tile
        if (j === columns - 1) {
          if (matchLength >= length) {
            // Found a horizontal cluster
            clusters.push(makeCluster(j + 1 - matchLength, i, matchLength, true));
          }

          // Reset cluster counter
          matchLength = 0;
        }
      } else {
        // Different type
        if (matchLength >= length) {
          // Found a horizontal cluster
          clusters.push(makeCluster(j - matchLength, i, matchLength, true));
        }

        // Reset cluster counter
        matchLength = 0;
      }
    }
  }

  return clusters;
};

/**
 * Find clusters with vertical direction
 * @param {Array} cells
 * @param {number} length matching length
 * @param {function} filter
 * @param {number} identity
 * @returns {Array} clusters
 */
export const findVerticalClusters = (cells, length, filter, identity) => {
  const rows = cells.length;
  const columns = cells[0].length;
  const clusters = [];

  for (let j = 0; j < columns; j += 1) {
    let matchLength = 0;

    for (let i = 0; i < rows; i += 1) {
      if (filter(cells, i, j, identity)) {
        // Identify an item
        matchLength += 1;

        // Last tile
        if (i === rows - 1) {
          if (matchLength >= length) {
            // Found a vertical cluster
            clusters.push(makeCluster(j, i + 1 - matchLength, matchLength, false));
          }

          // Reset cluster counter
          matchLength = 0;
        }
      } else {
        // Different type
        if (matchLength >= length) {
          // Found a vertical cluster
          clusters.push(makeCluster(j, i - matchLength, matchLength, false));
        }

        // Reset cluster counter
        matchLength = 0;
      }
    }
  }

  return clusters;
};

/**
 * Find clusters, from given cells, with given filters, identity and match length.
 * @param {Array} cells
 * @param {Number} length
 * @param {Function} horizontalIdentityFilter
 * @param {Function} verticalIdentityFilter
 * @param {Number} identity
 * @returns {Array} clusters
 */
export const findClustersByType = (cells, length, horizontalIdentityFilter, verticalIdentityFilter, identity) => {
  return [
    ...findHorizontalClusters(cells, length, horizontalIdentityFilter, identity),
    ...findVerticalClusters(cells, length, verticalIdentityFilter, identity)
  ];
};

/**
 * Find clusters, from given cells, with given filters, identities and match length.
 * @param {Array} cells
 * @param {Number} length
 * @param {Function} horizontalIdentityFilter
 * @param {Function} verticalIdentityFilter
 * @param {Array} identities
 * @returns {Array} clusters
 */
export const findClustersByTypes = (cells, length, horizontalIdentityFilter, verticalIdentityFilter, identities) => {
  const clusters = [];
  identities.forEach(identity => {
    clusters.push(...findClustersByType(cells, length, horizontalIdentityFilter, verticalIdentityFilter, identity));
  });

  return clusters;
};

/**
 * Do something that we actually no need
 * @param {*} clusters
 */
export const getClustersWidth = clusters => {
  const horizontalClusters = clusters.filter(cluster => cluster.horizontal);
  let horizontalStart = Number.POSITIVE_INFINITY;
  let horizontalEnd = 0;
  horizontalClusters.forEach(cl => {
    if (cl.column < horizontalStart) {
      horizontalStart = cl.column;
    }
    const end = cl.column + cl.length;
    if (end > horizontalEnd) {
      horizontalEnd = end;
    }
  });

  return horizontalEnd - horizontalStart;
};

/**
 * Do something that we actually no need
 * @param {*} clusters
 */
export const getClustersHorizontalInfimum = clusters => {
  const infimum = [];
  const width = getClustersWidth(clusters);
  const horizontalSortedClusters = clusters.filter(cluster => cluster.horizontal).sort((a, b) => b.row - a.row);
  for (let i = 0; i < horizontalSortedClusters.length; i += 1) {
    const cluster = horizontalSortedClusters[i];
    const { length } = cluster;
    for (let l = 0; l < length; l += 1) {
      const newColumn = cluster.column + l;
      if (!infimum.find(rc => rc[1] === newColumn)) {
        infimum.push([cluster.row, newColumn]);
      }
      if (infimum.length === width) {
        return infimum;
      }
    }
  }

  return infimum;
};

/**
 * Do something that we actually no need
 * @param {*} clusters
 */
export const getClustersInfimum = clusters => {
  const infimum = getClustersHorizontalInfimum(clusters);
  clusters
    .filter(cluster => !cluster.horizontal)
    .forEach(cluster => {
      const i = infimum.find(rc => rc[1] === cluster.column);
      const clusterBottomRow = cluster.row + cluster.length - 1;
      if (i) {
        if (clusterBottomRow > i[0]) {
          i[0] = clusterBottomRow;
        }
      } else {
        infimum.push([clusterBottomRow, cluster.column]);
      }
    });

  return infimum;
};
