import { lego } from '@armathai/lego';
import { store } from '../../models/store';
import { setItemAlertCommand } from '../board/set-item-alert-command';

export function onItemIsOutOfBoundsCommand(itemView) {
  lego.command.payload(itemView.uuid, true).execute(setItemAlertCommand);
  store.game.boardModel.stopCellsHighlight();
  store.game.boardModel.getItem(itemView.uuid).hide = false;
}
