import { lego } from '@armathai/lego';
import { AdStatus } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';

let sfx;
let gameTheme;

function play(id, volume = 1) {
  sfx.play(id, volume);
}

// function stop(id) {
//   sfx.stop(id);
// }

function playGameTheme() {
  soundLoader.play(gameTheme);
}

function stopGameTheme() {
  gameTheme.fadeTo(500, 0);
  gameTheme.onFadeComplete.add(() => {
    gameTheme.stop();
  });
}

function onLoadCompleteUpdate(complete) {
  if (!complete) {
    return;
  }

  sfx = soundLoader.addAudioSprite('audio');
  gameTheme = CI_API.game.add.audio('game-theme');
  gameTheme.loop = true;

  playGameTheme();
}

function onPlayFXAnimation() {
  play('button-click');
}

function onAdStatusUpdate(status) {
  switch (status) {
    case AdStatus.Cta:
      stopGameTheme();
      play('win');
      break;

    default:
      break;
  }
}

export function SoundObservant() {
  lego.event
    .on(ModelEvents.LoadModel.CompleteUpdate, onLoadCompleteUpdate, this)
    .on(ModelEvents.AdModel.StatusUpdate, onAdStatusUpdate, this)
    .on(ViewEvents.BoardView.PlayFXAnimation, onPlayFXAnimation, this)
    .on(ModelEvents.ItemModel.ActivateUpdate, onPlayFXAnimation, this)
    .on(ViewEvents.BoardView.GainNectarSequenceComplete, onPlayFXAnimation, this);
}
