import { ItemsConfig } from '../../../configs/item-config';
import { ObservableModel } from '../../observable-model';

export class ItemModel extends ObservableModel {
  constructor(type) {
    super('ItemModel');

    const { enabled } = ItemsConfig[type];

    this._type = type;
    this._remove = false;
    this._highlight = false;
    this._alert = false;
    this._enabled = enabled;
    this._magnet = null;
    this._hide = false;
    this._activate = false;

    this.makeObservable('_remove', '_highlight', '_alert', '_hide', '_magnet', '_enabled', '_activate');
  }

  get type() {
    return this._type;
  }

  get remove() {
    return this._remove;
  }

  set remove(value) {
    this._remove = value;
  }

  get enabled() {
    return this._enabled;
  }

  set enabled(value) {
    this._enabled = value;
  }

  get alert() {
    return this._alert;
  }

  set alert(value) {
    this._alert = value;
  }

  get highlight() {
    return this._highlight;
  }

  set highlight(value) {
    this._highlight = value;
  }

  get hide() {
    return this._hide;
  }

  set hide(value) {
    this._hide = value;
  }

  get magnet() {
    return this._magnet;
  }

  set magnet(value) {
    this._magnet = value;
  }

  get activate() {
    return this._activate;
  }

  set activate(value) {
    this._activate = value;
  }
}
