/* eslint-disable class-methods-use-this */

import { lego } from '@armathai/lego';
import { getCtaStylePlayButtonConfig } from '../../configs/button-configs';
import { getHandImageConfig } from '../../configs/image-configs';
import { getCTAPopupHeaderTextConfig } from '../../configs/text-configs';
import { ViewEvents } from '../../events/view-events';
import { delayRunnable, handScaleTw, makeImage } from '../../utils';
import { ButtonIcon } from '../../utils/button/button-icon';
import { Container } from '../../utils/container';
import { PopupComponent } from '../../utils/popup-component';

export class CTAPopup extends Container {
  constructor() {
    super();

    this._drawBounds();
    this._build();
  }

  destroy() {
    this.game.tweens.removeFrom(this);
    this.game.tweens.removeFrom(this.scale);
    super.destroy();
  }

  getBounds() {
    return this._gr.getBounds();
  }

  buildPlayButton(config) {
    return this._buildButton(config, this._onPlayClick);
  }

  buildRetryButton(config) {
    return this._buildButton(config, this._onRetryClick);
  }

  show(force) {
    if (force) {
      this.alpha = 1;
      return;
    }

    this.game.add
      .tween(this.scale)
      .from({ x: 0, y: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false)
      .onStart.add(() => {
        this.alpha = 1;
      });
  }

  _build() {
    this._buildPopup();
    this._buildBtn();
    this._buildHand();
  }

  _drawBounds() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x0000ff, 0);
    gr.drawRect(-320, -200, 640, 580);
    gr.endFill();

    this.addChild((this._gr = gr));
  }

  _buildBtn() {
    const btn = this.buildPlayButton(getCtaStylePlayButtonConfig());
    btn.position.set(0, 300);
    this.addChild((this._btn = btn));
  }

  _buildButton(config, callback) {
    const btn = new ButtonIcon(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  _buildHand() {
    this._hand = makeImage(getHandImageConfig());
    this._hand.alpha = 0;
    this._hand.position.set(this._btn.position.x + 80, this._btn.position.y + 107);

    this.addChild(this._hand);
    delayRunnable(800, this._startHandTween, this);
    // this._startHandTween();
  }

  _startHandTween() {
    const { x, y } = this._hand;

    this.game.add
      .tween(this._hand)
      .from({ x: x + 50, y: y + 50 }, 400, Phaser.Easing.Cubic.Out, true, 0)
      .onComplete.addOnce(() => {
        this._pointHand();
      });

    //

    const twAlpha = this.game.add.tween(this._hand).to({ alpha: 1 }, 400, Phaser.Easing.Cubic.Out, true, 0);
    twAlpha.universal = true;
  }

  _pointHand() {
    const tw = handScaleTw(this._hand, 0.15, 200, -1);
    tw.repeatDelay(1000);
    tw.universal = true;
    // this._moveHand();
  }

  _buildPopup() {
    const popup = new PopupComponent(this._getPopupConfig());
    popup.top = LP(-180, -180);
    this.addChild((this._popup = popup));
  }

  _getPopupConfig() {
    return {
      bgConfig: {
        width: LP(1200, 675),
        height: LP(675, 1200),
        textConfig: getCTAPopupHeaderTextConfig()
      },
      headerConfig: {
        width: 400,
        textConfig: getCTAPopupHeaderTextConfig()
      }
    };
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  _onRetryClick() {
    lego.event.emit(ViewEvents.CtaView.RetryClick);
  }
}
