import { CellScale } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getCTAGridLandscapeConfig() {
  // const popH = isSquareLikeScreen() ? 0.8 : 0.92;
  // const btnY = isSquareLikeScreen() ? popH : popH + 0.05;
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'popup',
        bounds: {
          x: isSquareLikeScreen() ? 0.41 : 0.35,
          y: isSquareLikeScreen() ? 0.13 : 0.06,
          width: 0.5,
          height: 0.8
        },
        scale: CellScale.ShowAll
      },
      {
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        name: 'blocker',
        scale: CellScale.Fill
      }
    ]
  };
}

export function getCTAGridPortraitConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'popup',
        bounds: { x: 0.03, y: 0.335, width: 0.94, height: 0.52 }
      },
      {
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        name: 'blocker',
        scale: CellScale.Fill
      }
    ]
  };
}
