/* eslint-disable class-methods-use-this */
import { getItemHighlightImageConfig, getItemImageConfig } from '../../../configs/image-configs';
import { ISO_ANGLE } from '../../../constants';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class ItemComponent extends Container {
  constructor(key, anchor) {
    super();
    this._anchor = anchor;
    this._key = key;
    this.angle = -ISO_ANGLE;
    this._build();
  }

  turnLeft() {
    this.scale.x = -1;
  }

  turnRight() {
    this.scale.x = 1;
  }

  startActivate() {
    //
  }

  stopActivate() {
    //
  }

  // eslint-disable-next-line class-methods-use-this
  startHighlight() {
    if (this._highlightStrokeTween) {
      return;
    }
    if (this._highlight) {
      this.game.tweens.remove(this._highlightStrokeTween);
      // this.game.tweens.remove(this._highlightShadowTween);
      this._highlight.alpha = 0;
      this._highlight.visible = true;
      this._highlightStrokeTween = this.game.add
        .tween(this._highlight)
        .to({ alpha: 1 }, 350, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
    }
  }

  stopHighlight() {
    if (this._highlight) {
      this.game.tweens.remove(this._highlightStrokeTween);
      this._highlight.alpha = 0;
      this._highlight.visible = false;
      this._highlightStrokeTween = null;
    }
  }

  startAlert() {
    // this._stroke && (this._stroke.tint = 0xff0000);
  }

  stopAlert() {
    // this._stroke && (this._stroke.tint = 0xffffff);
  }

  startMagnet(dX, dY) {
    this.stopMagnet();
    this._magnetTwn = this.game.add
      .tween(this)
      .to({ x: dX * 10, y: dY * 8 }, 300, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
  }

  stopMagnet() {
    this.game.tweens.remove(this._magnetTwn);
    this.position.set(0, 0);
  }

  _build() {
    this._buildHighlight();
    this._buildItem();
  }

  _buildItem() {
    const img = makeImage(getItemImageConfig(this._key));
    img.anchor.set(this._anchor.x, this._anchor.y);
    this.addChild((this._img = img));
  }

  _buildHighlight() {
    const highlight = makeImage(getItemHighlightImageConfig());
    highlight.anchor.set(this._anchor.x, this._anchor.y);
    highlight.position.set(-5, 20);
    highlight.alpha = 0;
    highlight.visible = false;
    this.addChild((this._highlight = highlight));
  }
}
