import { fitText, makeImage, makeNinePatch, makeText } from '..';
import { getBtnIconImageConfig } from '../../configs/image-configs';
import { Button } from './button';

export class ButtonIcon extends Button {
  createState(config) {
    const bttType = CI_API.Globals.PARAMS.cta_button_style;
    let fit = null;
    let scale = 0.6;
    let smallText = false;
    if (bttType === 'text and icon') {
      fit = 0.5;

      smallText = true;
    } else if (bttType === 'icon') {
      fit = 0.001;
      scale = 0.9;
      smallText = false;
    }
    //

    // this.alpha = 0;
    const state = new Phaser.Group(this.game);

    const { bg, label, fitWidth = 0.9, fitHeight = 0.9 } = config;

    // FRAME
    const bgObj = bg.width && bg.height ? makeNinePatch(bg) : makeImage(bg);
    if (!fit) fit = Math.min(fitWidth, fitHeight);

    state.add(bgObj);
    // FRAME
    const iconObj = makeImage(getBtnIconImageConfig(CI_API.Globals.PARAMS.cta_button_icon));
    iconObj.scale.set(scale);
    if (smallText) iconObj.position.x = 90;

    if (fit < 0.7) state.add(iconObj);

    // LABEL
    if (label) {
      const labelObj = makeText(label);
      labelObj.anchor.set(0.5);
      state.add(labelObj);
      fitText(labelObj, bgObj.width * fit, bgObj.height * fit);
      if (smallText) labelObj.position.x = -47;
    }
    this.addTween();
    return this.add(state);
  }

  addTween() {
    const tw = this.game.add
      .tween(this.scale)
      .to({ x: 1.1, y: 1.1 }, 1200, Phaser.Easing.Linear.None, true, 0, -1, true);
    tw.universal = true;
  }
}
