import { lego } from '@armathai/lego';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import {
  delayRunnable,
  getViewByProperty,
  handMoveTw,
  handScaleTw,
  removeRunnable,
  searchAtlasByFrame
} from '../../utils';
import { sample } from '../../utils/array/sample';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.05, 0.1);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hide();
  }

  destroy() {
    this._removeTw();
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    this._removeTw();
    this._showTapHint();
    // if (CI_API.Globals.PARAMS.tap_to_merge) {
    //   this._showTapHint();
    // } else {
    //   this._showDragHint();
    // }
  }

  _firstTimeShow() {
    const point = this._positions[this._currentPoint];
    this.scale.set(1);
    this.alpha = 1;
    this.position.set(point.x, point.y);
    this.angle = 0;
    this.visible = true;
    const { x, y, angle } = this;

    this.game.add
      .tween(this)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0)
      .onComplete.addOnce(() => {
        this._pointHand();
      });
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
  }

  _pointHand() {
    handScaleTw(this).onComplete.addOnce(() => {
      this._currentPoint += 1;
      if (this._currentPoint === this._positions.length) {
        this._currentPoint = 0;
      }
      this._moveHand(this._positions[this._currentPoint]);
    });
  }

  _moveHand(pos) {
    handMoveTw(this, pos).onComplete.addOnce(() => {
      this._pointHand();
    });
  }

  _hide() {
    this._removeTw();
    removeRunnable(this._hideRunnable);
    this.visible = false;
  }

  _getHintPositions() {
    const { boardModel } = store.game;
    const items = boardModel.getHintItems();

    const { uuid } = sample(items);
    const { position, parent } = getViewByProperty('uuid', uuid);

    const pos = this.parent.toLocal(parent.toGlobal(position), undefined);
    return [pos];
  }

  _removeTw() {
    this.game.tweens.removeFrom(this.scale);
    this.game.tweens.removeFrom(this);
    this.game.tweens.remove(this._handMoveTwn);
    this.game.tweens.remove(this._moveTwn);
  }

  _showDragHint() {
    const pair = store.game.boardModel.getItemsToMerge();
    if (pair.length) {
      const fromItem = getViewByProperty('uuid', pair[0]);
      const toItem = getViewByProperty('uuid', pair[1]);

      const { position: fromPos, parent } = fromItem;
      const { position: toPos } = toItem;

      const fromLocalPos = this.parent.toLocal(parent.toGlobal(fromPos), undefined);
      const toLocalPos = this.parent.toLocal(parent.toGlobal(toPos), undefined);

      this._startMoveTween(toLocalPos.x, toLocalPos.y, fromLocalPos.x, fromLocalPos.y);
    } else {
      this._showTapHint();
    }
  }

  _showTapHint() {
    this._positions = this._getHintPositions();
    this._currentPoint = 0;

    this._firstTimeShow();
  }

  _startMoveTween(toX, toY, fromX, fromY) {
    this.position.set(fromX + 30, fromY + 30);
    this.alpha = 1;
    this.scale.set(1);
    this.visible = true;
    this.game.tweens.removeFrom(this);
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.InOut, true);
    this._handMoveTwn = this.game.add.tween(this).to({ x: fromX, y: fromY }, 400, Phaser.Easing.Cubic.Out, true);

    this._handMoveTwn.onComplete.add(() => {
      this._startPushTween().onComplete.add(() => {
        this._startDragTw(toX, toY, fromX, fromY);
      });
    });
  }

  _startPushTween() {
    return this.game.add.tween(this.scale).to({ x: '-0.2', y: '-0.2' }, 200, Phaser.Easing.power2, true);
  }

  _startDragTw(toX, toY, fromX, fromY) {
    this._moveTwn = this.game.add.tween(this).to({ x: toX, y: toY }, 700, Phaser.Easing.Cubic.InOut, true, 100);

    this._moveTwn.onComplete.addOnce(() => {
      this.game.add.tween(this).to({ alpha: 0 }, 200, Phaser.Easing.Cubic.InOut, true);
      removeRunnable(this._hideRunnable);

      this._hideRunnable = delayRunnable(500, () => {
        this._startMoveTween(toX, toY, fromX, fromY);
      });
    });
  }
}
