import { lego, not } from '@armathai/lego';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { boardIdleGuard } from '../../guards/board/board-idle-guard';
import { boardMergeStartGuard } from '../../guards/board/board-merge-start-guard';
import { boardNectarGainGuard } from '../../guards/board/board-start-nectar-gain-guard';
import { hideHintCommand } from '../ad/hint/hide-hint-command';
import { startHintVisibilityTimerCommand } from '../ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from '../ad/hint/stop-hint-visibility-timer-command';
import { updateItemsPairsCommand } from '../ad/hint/update-items-pair-command';
import { completeTutorialSequenceCommand } from '../ad/tutorial/complete-tutorial-sequence-command';
import { stopItemsHighlightCommand } from './stop-items-highlight-command';

export function onBoardStateUpdateCommand() {
  lego.command
    //
    .guard(boardIdleGuard)
    .execute(updateItemsPairsCommand, stopItemsHighlightCommand)

    .guard(hintModelGuard, boardIdleGuard)
    .execute(startHintVisibilityTimerCommand)

    .guard(hintModelGuard, not(boardIdleGuard))
    .execute(hideHintCommand, stopHintVisibilityTimerCommand)

    .guard(tutorialModelGuard, boardMergeStartGuard)
    .execute(completeTutorialSequenceCommand)

    .guard(tutorialModelGuard, boardNectarGainGuard)
    .execute(completeTutorialSequenceCommand);
}
