// eslint-disable-next-line import/no-extraneous-dependencies
import { CellScale } from '@armathai/phaser2-grid';

export function getGameGridLandscapeConfig() {
  return {
    name: 'game',
    // debug: { color: 0x2fc900 },

    cells: [
      {
        name: 'board',
        scale: CellScale.Envelop,
        bounds: { x: 0, y: 0.1, width: 1, height: 1 }
      }
    ]
  };
}

export function getGameGridPortraitConfig() {
  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'board',
        scale: CellScale.Envelop,
        bounds: { x: -0.1, y: 0, width: 1, height: 1 }
      }
    ]
  };
}
